
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body{
  margin:0;
}

#rootButton{
  position:fixed;
  bottom:3em;
  width:4em;
  height:4em;
  left:0;
  right:0;
  margin:0 auto;
  text-align:center;
  z-index: 500;
  border-radius: 100%;
  background-color: green;
}
.sub-buttons{
  margin-top:20em;
}
.sub-button{
  float:left;
  margin:0.5em;
  width:3em;
  height:3em;
  z-index:500;
  border-radius: 100%;
  background-color: green;
}
#mask{
  position:fixed;
  top:0;
  width:100%;
  height:100%;
  z-index: 50;
  background-color: rgba(0,0,0,0.5);
}
